'use client';

import Card from '@/components/ui/Card';
import { projects } from '@/data/projects';
import { motion } from 'framer-motion';

const container = {
  hidden: { opacity: 0 },
  show: {
    opacity: 1,
    transition: {
      staggerChildren: 0.1
    }
  }
};

const item = {
  hidden: { opacity: 0, y: 20 },
  show: { opacity: 1, y: 0 }
};

export default function ProjectGrid() {
  // Group projects by status
  const completed = projects.filter(project => project.status === 'Completed');
  const inProgress = projects.filter(project => project.status === 'In Progress');
  const notStarted = projects.filter(project => project.status === 'Not Started').slice(0, 2); // Only show first two not started

  return (
    <section className="py-20 px-4 sm:px-6 lg:px-8">
      <div className="max-w-7xl mx-auto">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="mb-16 bg-background-darker p-8 rounded-xl border border-gray-800 shadow-xl text-center"
        >
          <h2 className="text-3xl font-bold text-text-primary mb-4">Featured Projects</h2>
          <p className="text-text-secondary max-w-3xl mx-auto">
            Explore my hands-on projects as I build practical skills in LLM development, fine-tuning, and application.
          </p>
        </motion.div>
        
        {/* Completed Projects */}
        {completed.length > 0 && (
          <div className="mb-16">
            <div className="flex items-center mb-8">
              <div className="h-4 w-4 rounded-full bg-success mr-3"></div>
              <h3 className="text-2xl font-bold text-text-primary">Completed</h3>
            </div>
            
            <motion.div
              variants={container}
              initial="hidden"
              animate="show"
              className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6"
            >
              {completed.map((project) => (
                <motion.div key={project.id} variants={item} className="h-full">
                  <Card
                    title={project.title}
                    description={project.description}
                    status={project.status}
                    githubLink={project.githubLink}
                    detailsLink={project.detailsLink}
                    tags={project.tags}
                    month={project.month}
                    icon={project.icon}
                  />
                </motion.div>
              ))}
            </motion.div>
          </div>
        )}
        
        {/* In Progress Projects */}
        {inProgress.length > 0 && (
          <div className="mb-16">
            <div className="flex items-center mb-8">
              <div className="h-4 w-4 rounded-full bg-primary mr-3"></div>
              <h3 className="text-2xl font-bold text-text-primary">In Progress</h3>
            </div>
            
            <motion.div
              variants={container}
              initial="hidden"
              animate="show"
              className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6"
            >
              {inProgress.map((project) => (
                <motion.div key={project.id} variants={item} className="h-full">
                  <Card
                    title={project.title}
                    description={project.description}
                    status={project.status}
                    githubLink={project.githubLink}
                    detailsLink={project.detailsLink}
                    tags={project.tags}
                    month={project.month}
                    icon={project.icon}
                  />
                </motion.div>
              ))}
            </motion.div>
          </div>
        )}
        
        {/* Upcoming Projects (Not Started) */}
        {notStarted.length > 0 && (
          <div>
            <div className="flex items-center mb-8">
              <div className="h-4 w-4 rounded-full bg-gray-600 mr-3"></div>
              <h3 className="text-2xl font-bold text-text-primary">Coming Up Next</h3>
            </div>
            
            <motion.div
              variants={container}
              initial="hidden"
              animate="show"
              className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6"
            >
              {notStarted.map((project) => (
                <motion.div key={project.id} variants={item} className="h-full">
                  <Card
                    title={project.title}
                    description={project.description}
                    status={project.status}
                    githubLink={project.githubLink}
                    detailsLink={project.detailsLink}
                    tags={project.tags}
                    month={project.month}
                    icon={project.icon}
                  />
                </motion.div>
              ))}
            </motion.div>
          </div>
        )}
        
        {/* View All Button */}
        <div className="mt-16 text-center">
          <motion.a
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            href="/milestones"
            className="inline-flex items-center gap-2 px-6 py-3 bg-background-dark hover:bg-gray-800 text-text-primary font-medium rounded-lg border border-gray-700 transition-colors"
          >
            View All Projects
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth="2">
              <path strokeLinecap="round" strokeLinejoin="round" d="M14 5l7 7-7 7" />
            </svg>
          </motion.a>
        </div>
      </div>
    </section>
  );
} 